@import "../../styles/vars.scss";

.chat {
    flex: 2;

    .chatInfo {
        height: 50px;
        background-color: $lightPurpur1;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
        color: lightgray;
        font-size: 20px;

        .openButton {
            cursor: pointer;
            display: block;
        }
    }

    .chatIcons {
        display: flex;
        gap: 10px;

        img {
            height: 24px;
            cursor: pointer;
        }
    }

    .messages {
        background-color: $lightBlue1;
        padding: 10px;
        height: calc(100% - 100px);
        overflow-y: scroll;

        .notChat {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            color: gray;
        }

        .messagesDate {
            text-align: center;
            color: gray;
        }


        .message {
            display: flex;
            gap: 20px;
            margin-bottom: 20px;

            .messageInfo {
                display: flex;
                flex-direction: column;
                color: gray;
                font-weight: 300;

                img {
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    object-fit: cover;
                }
            }

            .messageContent {
                max-width: 80%;
                display: flex;
                flex-direction: column;
                gap: 10px;



                p {
                    background-color: #fff;
                    padding: 10px 20px;
                    border-radius: 0px 10px 10px 10px;
                    max-width: max-content;
                }

                img {
                    width: 50%;
                }
            }

            &.owner {
                flex-direction: row-reverse;

                .messageContent {
                    align-items: flex-end;

                    p {
                        background-color: $lightBlue3;
                        color: #fff;
                        border-radius: 10px 0 10px 10px;
                    }
                }
            }
        }


    }

    .input {
        height: 50px;
        background-color: white;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        input {
            width: 100%;
            border: none;
            outline: none;
            color: $darkColor;
            font-size: 18px;

            &::placeholder {
                color: lightgray;
            }
        }

        .send {
            display: flex;
            align-items: center;
            gap: 10px;

            img {
                height: 24px;
                cursor: pointer;
            }

            button {
                border: none;
                padding: 10px 15px;
                color: white;
                background-color: $lightBlue3;
                cursor: pointer;
            }
        }
    }
}